import React, { useState, useEffect } from "react"
import "./style.css"
import "./gst.css"
import Layout from "../layout/layout"

const Gstcalculator = () => {
  const [isGSTInclusive, setIsGSTInclusive] = useState(true)
  const [inclusiveTotalCost, setInclusiveTotalCost] = useState(0)
  const [inclusiveGSTPercent, setInclusiveGSTPercent] = useState(0)
  const [exclusiveTotalCost, setExclusiveTotalCost] = useState(0)
  const [exclusiveGSTPercent, setExclusiveGSTPercent] = useState(0)
  const [totalCost, setTotalCost] = useState(0)
  const [totalGST, setTotalGST] = useState(0)
  const [totalProfit, setTotalProfit] = useState(0)

  const handleRadioChange = e => {
    setIsGSTInclusive(e.target.value === "inclusive")
  }

  const handleInclusiveTotalCostChange = e => {
    setInclusiveTotalCost(e.target.value)
  }

  const handleInclusiveGSTPercentChange = e => {
    setInclusiveGSTPercent(parseFloat(e.target.value))
  }

  const handleExclusiveTotalCostChange = e => {
    setExclusiveTotalCost(e.target.value)
  }

  const handleExclusiveGSTPercentChange = e => {
    setExclusiveGSTPercent(parseFloat(e.target.value))
  }

  useEffect(() => {
    if (isGSTInclusive) {
      setTotalCost(inclusiveTotalCost)
      setTotalGST((inclusiveTotalCost * inclusiveGSTPercent) / 100)
      setTotalProfit((inclusiveTotalCost * (inclusiveGSTPercent / 100)) / 100)
    } else {
      setTotalCost(exclusiveTotalCost)
      setTotalGST((exclusiveTotalCost * exclusiveGSTPercent) / 100)
      setTotalProfit(0)
    }
  }, [
    isGSTInclusive,
    inclusiveTotalCost,
    inclusiveGSTPercent,
    exclusiveTotalCost,
    exclusiveGSTPercent,
  ])

  return (
    <Layout>
      <section className="gst-box">
        <div className="gst-container">
          <div className="left-side">
            <h3 className="gst-heading">
              GST Calculator: Calculate GST Amount
            </h3>
            <p className="para1">Don't know how to calculate GST?</p>
            <p className="para2">
              Get the best GST calculator in India at Vakilsearch. Easy GST
              calculation in seconds, anytime and anywhere!
            </p>

            <div className="radio-buttons">
              <input
                className="radio-1"
                type="radio"
                name="gst"
                value="inclusive"
                checked={isGSTInclusive}
                onChange={handleRadioChange}
              />
              <label className="label" htmlFor="inclusive">
                GST Inclusive
              </label>
              <input
                className="radio-2"
                type="radio"
                name="gst"
                value="exclusive"
                checked={!isGSTInclusive}
                onChange={handleRadioChange}
              />{" "}
              <label className="label" htmlFor="exclusive">
                GST exclusive
              </label>
            </div>
            {isGSTInclusive ? (
              <div className="given-input">
                <label htmlFor="cost-input">Cost of Goods/Services</label>
                <div className="user-input-rs">
                  <input
                    type="number"
                    onChange={handleInclusiveTotalCostChange}
                  />
                  <p className="ruppes-symbol">₹</p>
                </div>
              </div>
            ) : (
              <div className="given-input">
                <label htmlFor="cost-input">Cost of Goods/Services</label>

                <div className="user-input-rs">
                  <input
                    type="number"
                    onChange={handleInclusiveTotalCostChange}
                  />
                  <p className="ruppes-symbol">₹</p>
                </div>
              </div>
            )}

            {isGSTInclusive ? (
              <div className="given-input">
                <label htmlFor="gst-rate">Select Gst Rate</label>
                <div className="gst-values">
                  <select onChange={handleInclusiveGSTPercentChange}>
                    <option value="0">Select GST Percent</option>
                    <option value="3">3%</option>
                    <option value="5">5%</option>
                    <option value="12">12%</option>
                    <option value="18">18%</option>
                    <option value="28">28%</option>
                  </select>
                </div>
              </div>
            ) : (
              <div className="given-input">
                <label htmlFor="gst-rate">Select Gst Rate</label>
                <div className="gst-values">
                  <select onChange={handleExclusiveGSTPercentChange}>
                    <option value="0">Select GST Percent</option>
                    <option value="3">3%</option>
                    <option value="5">5%</option>
                    <option value="12">12%</option>
                    <option value="18">18%</option>
                    <option value="28">28%</option>
                  </select>
                </div>
              </div>
            )}
          </div>
          <div className="right-side">
            <div className="right-container">
              <div className="selling-cost">
                <p>Total Cost of Selling</p>
                <p>₹ {totalCost}</p>
              </div>

              <div className="total-gst">
                <p>Total GST</p>
                <p>₹ {totalGST}</p>
              </div>
              <div className="total-profit">
                {isGSTInclusive && <p>Total Profit</p>}
                {isGSTInclusive && <p>₹ {totalProfit}</p>}
              </div>
            </div>
          </div>
        </div>
      </section>
    </Layout>
  )
}
export default Gstcalculator
